import React, { useState } from 'react';
import { useQuery } from '@tanstack/react-query';
import {
    Card,
    CardContent,
    Grid,
    Typography,
    CircularProgress,
    Alert,
    Box,
    TextField,
    Button,
    Paper,
    IconButton,
    Tooltip,
    ToggleButton,
    ToggleButtonGroup,
} from '@mui/material';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import WeatherTimePlot from './WeatherTimePlot';
import { fetchWeatherData, searchLocation } from '../api/weatherApi';
import { Location } from '../types/weather';
import { useTheme } from '../context/ThemeContext';

const defaultLocation: Location = {
    lat: 47.6062,
    lng: -122.3321,
    name: 'Seattle, WA'
};

const getWeatherIcon = (weather: string) => {
    const icons: { [key: string]: string } = {
        'clearday': '☀️',
        'clearnight': '🌙',
        'pcloudyday': '🌤️',
        'pcloudynight': '☁️',
        'mcloudyday': '⛅',
        'mcloudynight': '☁️',
        'cloudyday': '☁️',
        'cloudynight': '☁️',
        'humidday': '💧',
        'humidnight': '💧',
        'lightrainday': '🌦️',
        'lightrainnight': '🌧️',
        'oshowerday': '🌦️',
        'oshowernight': '🌧️',
        'ishowerday': '🌦️',
        'ishowernight': '🌧️',
        'lightsnowday': '🌨️',
        'lightsnownight': '🌨️',
        'rainday': '🌧️',
        'rainnight': '🌧️',
        'snowday': '🌨️',
        'snownight': '🌨️',
        'rainsnowday': '🌨️',
        'rainsnownight': '🌨️',
        'tsday': '⛈️',
        'tsnight': '⛈️',
        'tsrainday': '⛈️',
        'tsrainnight': '⛈️',
    };
    return icons[weather] || '❓';
};

const getWindSpeedText = (speed: number): string => {
    const speeds = [
        'Calm',
        'Light',
        'Moderate',
        'Fresh',
        'Strong',
        'Gale',
        'Storm',
        'Hurricane'
    ];
    return speeds[speed - 1] || 'Unknown';
};

const getPrecipitationAmount = (amount: number): string => {
    const amounts = [
        'None',
        '0-0.25mm/hr',
        '0.25-1mm/hr',
        '1-4mm/hr',
        '4-10mm/hr',
        '10-16mm/hr',
        '16-30mm/hr',
        '30-50mm/hr',
        '50-75mm/hr',
        'Over 75mm/hr'
    ];
    return amounts[amount] || 'Unknown';
};

const getCloudCoverText = (cover: number): string => {
    const covers = [
        'Clear (0-6%)',
        'Mostly Clear (6-19%)',
        'Partly Cloudy (19-31%)',
        'Partly Cloudy (31-44%)',
        'Partly Cloudy (44-56%)',
        'Mostly Cloudy (56-69%)',
        'Mostly Cloudy (69-81%)',
        'Cloudy (81-94%)',
        'Overcast (94-100%)'
    ];
    return covers[cover - 1] || 'Unknown';
};

const getLiftedIndexText = (index: number): string => {
    if (index < -7) return 'Severe Thunderstorm Possible (Below -7)';
    if (index <= -5) return 'Thunderstorm Possible (-7 to -5)';
    if (index <= -3) return 'Thunderstorm Possible (-5 to -3)';
    if (index <= 0) return 'Thunderstorm Possible (-3 to 0)';
    if (index <= 4) return 'Stable (0 to 4)';
    if (index <= 8) return 'Stable (4 to 8)';
    if (index <= 11) return 'Very Stable (8 to 11)';
    return 'Extremely Stable (Over 11)';
};

const getWindProfileText = (profile: { direction: string; speed: number }[]): string => {
    if (!profile || profile.length === 0) return 'No wind profile data';
    return profile.map((wind, index) =>
        `${index + 1}km: ${getWindSpeedText(wind.speed)} ${wind.direction}`
    ).join(', ');
};

const getCloudProfileText = (profile: number[]): string => {
    if (!profile || profile.length === 0) return 'No cloud profile data';
    return profile.map((cover, index) =>
        `${index + 1}km: ${getCloudCoverText(cover)}`
    ).join(', ');
};

const getPrecipitationType = (type: string): string => {
    const types: { [key: string]: string } = {
        'none': 'None',
        'rain': 'Rain',
        'snow': 'Snow',
        'rainsnow': 'Rain and Snow',
        'frzr': 'Freezing Rain',
        'icep': 'Ice Pellets',
        'frzs': 'Freezing Drizzle',
        'drizzle': 'Drizzle',
        'showers': 'Showers',
        'thunderstorm': 'Thunderstorm'
    };
    return types[type.toLowerCase()] || type;
};

const Weather: React.FC = () => {
    const { isDarkMode, toggleTheme } = useTheme();
    const [location, setLocation] = useState<Location>(defaultLocation);
    const [searchQuery, setSearchQuery] = useState('');
    const [isSearching, setIsSearching] = useState(false);
    const [isGettingLocation, setIsGettingLocation] = useState(false);
    const [temperatureUnit, setTemperatureUnit] = useState<'C' | 'F'>('C');
    const [fetchTimestamp, setFetchTimestamp] = useState<number>(Date.now());

    const convertTemperature = (celsius: number): number => {
        if (temperatureUnit === 'F') {
            return (celsius * 9 / 5) + 32;
        }
        return celsius;
    };

    const { data: weatherData, isLoading: isLoadingWeather, error } = useQuery({
        queryKey: ['weather', location],
        queryFn: async () => {
            const data = await fetchWeatherData(location);
            setFetchTimestamp(Date.now());
            return data;
        },
        refetchInterval: 1000 * 60 * 60, // Refetch every hour
    });

    const handleGetCurrentLocation = () => {
        if (!navigator.geolocation) {
            alert('Geolocation is not supported by your browser. Please try searching for a location instead.');
            return;
        }

        setIsGettingLocation(true);
        navigator.geolocation.getCurrentPosition(
            (position) => {
                setLocation({
                    lat: position.coords.latitude,
                    lng: position.coords.longitude,
                    name: 'Current Location'
                });
                setIsGettingLocation(false);
                setSearchQuery(`${position.coords.latitude.toFixed(4)},${position.coords.longitude.toFixed(4)}`);
            },
            (error) => {
                console.error('Error getting location:', error);
                let errorMessage = 'Error getting your location. ';

                switch (error.code) {
                    case error.PERMISSION_DENIED:
                        errorMessage += 'Please enable location services in your browser settings and try again.';
                        break;
                    case error.POSITION_UNAVAILABLE:
                        errorMessage += 'Location information is unavailable. Please try searching for a location instead.';
                        break;
                    case error.TIMEOUT:
                        errorMessage += 'The request to get your location timed out. Please try again.';
                        break;
                    default:
                        errorMessage += 'Please try searching for a location instead.';
                }

                alert(errorMessage);
                setIsGettingLocation(false);
            },
            {
                enableHighAccuracy: true,
                timeout: 10000,
                maximumAge: 0
            }
        );
    };

    const handleSearch = async () => {
        if (!searchQuery.trim()) return;

        setIsSearching(true);
        try {
            const data = await searchLocation(searchQuery);

            if (data && data.length > 0) {
                setLocation({
                    lat: parseFloat(data[0].lat),
                    lng: parseFloat(data[0].lon),
                    name: data[0].display_name
                });
            } else {
                alert('Location not found. Please try a different search term.');
            }
        } catch (error) {
            console.error('Error geocoding location:', error);
            alert('Error searching for location. Please try again.');
        } finally {
            setIsSearching(false);
        }
    };

    return (
        <Box>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h4">
                    Weather
                </Typography>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <ToggleButtonGroup
                        value={temperatureUnit}
                        exclusive
                        onChange={(_, newUnit) => newUnit && setTemperatureUnit(newUnit)}
                        size="small"
                    >
                        <ToggleButton value="C">°C</ToggleButton>
                        <ToggleButton value="F">°F</ToggleButton>
                    </ToggleButtonGroup>
                    <Tooltip title={`Switch to ${isDarkMode ? 'light' : 'dark'} mode`}>
                        <IconButton onClick={toggleTheme} color="inherit">
                            {isDarkMode ? <Brightness7Icon /> : <Brightness4Icon />}
                        </IconButton>
                    </Tooltip>
                </Box>
            </Box>

            <Box sx={{ mb: 4 }}>
                <Grid container spacing={2} alignItems="center">
                    <Grid item xs>
                        <TextField
                            fullWidth
                            label="Search Location"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
                            placeholder="Enter a city, address, or landmark"
                            InputProps={{
                                startAdornment: (
                                    <Tooltip title="Use Current Location">
                                        <IconButton
                                            onClick={handleGetCurrentLocation}
                                            disabled={isGettingLocation}
                                            color="primary"
                                            size="small"
                                            sx={{ mr: 1 }}
                                        >
                                            {isGettingLocation ? (
                                                <CircularProgress size={20} />
                                            ) : (
                                                <LocationOnIcon />
                                            )}
                                        </IconButton>
                                    </Tooltip>
                                ),
                            }}
                        />
                    </Grid>
                    <Grid item>
                        <Button
                            variant="contained"
                            onClick={handleSearch}
                            disabled={isSearching}
                        >
                            {isSearching ? 'Searching...' : 'Search'}
                        </Button>
                    </Grid>
                </Grid>
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                    Current Location: {location.name}
                </Typography>
            </Box>

            {error ? (
                <Alert severity="error">
                    Error loading weather data: {error instanceof Error ? error.message : 'Unknown error'}
                </Alert>
            ) : isLoadingWeather ? (
                <Box display="flex" justifyContent="center" alignItems="center" minHeight="300px">
                    <CircularProgress />
                </Box>
            ) : (
                <>
                    <Paper elevation={2} sx={{ p: 3, mb: 4 }}>
                        <Typography variant="h5" gutterBottom>
                            Trends
                        </Typography>
                        {weatherData && (
                            <>
                                <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                                    Last updated: {new Date(fetchTimestamp).toLocaleString()}
                                </Typography>
                                <WeatherTimePlot
                                    data={weatherData}
                                    temperatureUnit={temperatureUnit}
                                    fetchTimestamp={fetchTimestamp}
                                />
                            </>
                        )}
                    </Paper>

                    <Grid container spacing={3}>
                        {weatherData?.map((data) => (
                            <Grid item xs={12} sm={6} md={4} key={data.timepoint}>
                                <Card
                                    sx={{
                                        height: '100%',
                                        transition: 'transform 0.2s',
                                        '&:hover': {
                                            transform: 'translateY(-4px)',
                                            boxShadow: 3,
                                        },
                                    }}
                                >
                                    <CardContent>
                                        <Typography variant="h5" component="h2" gutterBottom>
                                            {new Date(fetchTimestamp + data.timepoint * 3600000).toLocaleDateString(undefined, { weekday: 'long' })}
                                        </Typography>
                                        <Typography variant="h6" component="h2" gutterBottom>
                                            {new Date(fetchTimestamp + data.timepoint * 3600000).toLocaleDateString()}
                                        </Typography>
                                        <Typography variant="h6" component="h2" gutterBottom>
                                            {new Date(fetchTimestamp + data.timepoint * 3600000).toLocaleTimeString()}
                                        </Typography>
                                        <Typography variant="h2" component="p" color="primary" gutterBottom>
                                            {getWeatherIcon(data.weather)}
                                        </Typography>
                                        <Typography variant="h5" component="p" gutterBottom>
                                            {convertTemperature(data.temp2m).toFixed(1)}°{temperatureUnit}
                                        </Typography>
                                        <Typography variant="body2" color="text.secondary">
                                            Cloud Cover: {getCloudCoverText(data.cloudcover)}
                                        </Typography>
                                        <Typography variant="body2" color="text.secondary">
                                            Humidity: {data.rh2m}%
                                        </Typography>
                                        <Typography variant="body2" color="text.secondary">
                                            Wind: {getWindSpeedText(data.wind10m.speed)} {data.wind10m.direction}
                                        </Typography>
                                        <Typography variant="body2" color="text.secondary">
                                            Precipitation: {getPrecipitationType(data.prec_type)} ({getPrecipitationAmount(data.prec_amount)})
                                        </Typography>
                                        <Typography variant="body2" color="text.secondary">
                                            Atmospheric Stability: {getLiftedIndexText(data.lifted_index)}
                                        </Typography>
                                        {data.msl_pressure && (
                                            <Typography variant="body2" color="text.secondary">
                                                Pressure: {data.msl_pressure} hPa
                                            </Typography>
                                        )}
                                        {typeof data.snow_depth !== 'undefined' && (
                                            <Typography variant="body2" color="text.secondary">
                                                Snow Depth: {data.snow_depth} cm
                                            </Typography>
                                        )}
                                        {data.seeing && (
                                            <Typography variant="body2" color="text.secondary">
                                                Astronomical Seeing: {data.seeing}/10
                                            </Typography>
                                        )}
                                        {data.transparency && (
                                            <Typography variant="body2" color="text.secondary">
                                                Atmospheric Transparency: {data.transparency}/10
                                            </Typography>
                                        )}
                                        {data.cloud_profile && (
                                            <Typography variant="body2" color="text.secondary">
                                                Cloud Profile: {getCloudProfileText(data.cloud_profile)}
                                            </Typography>
                                        )}
                                        {data.wind_profile && (
                                            <Typography variant="body2" color="text.secondary">
                                                Wind Profile: {getWindProfileText(data.wind_profile)}
                                            </Typography>
                                        )}
                                        {data.temp_profile && (
                                            <Typography variant="body2" color="text.secondary">
                                                Temperature Profile: {data.temp_profile.map((temp, index) =>
                                                    `${index + 1}km: ${convertTemperature(temp).toFixed(1)}°${temperatureUnit}`
                                                ).join(', ')}
                                            </Typography>
                                        )}
                                        {data.wind_profile_high_res && (
                                            <Typography variant="body2" color="text.secondary">
                                                High Res Wind: {getWindProfileText(data.wind_profile_high_res)}
                                            </Typography>
                                        )}
                                        {data.rh_profile_high_res && (
                                            <Typography variant="body2" color="text.secondary">
                                                High Res Humidity: {data.rh_profile_high_res.map((rh, i) =>
                                                    `${i + 1}km: ${rh}%`
                                                ).join(', ')}
                                            </Typography>
                                        )}
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                    <Box sx={{ mt: 4, textAlign: 'center' }}>
                        <Typography variant="body2" color="text.secondary">
                            Weather data provided by{' '}
                            <a
                                href="https://www.7timer.info/doc.php"
                                target="_blank"
                                rel="noopener noreferrer"
                                style={{ color: 'inherit', textDecoration: 'underline' }}
                            >
                                7Timer!
                            </a>
                        </Typography>
                    </Box>
                </>
            )}
        </Box>
    );
};

export default Weather; 